import * as React from "react";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import Button from "@mui/material/Button";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import InputLabel from "@mui/material/InputLabel";
import FormControl from "@mui/material/FormControl";
import Divider from "@mui/material/Divider";
import KeyboardArrowRightIcon from "@mui/icons-material/KeyboardArrowRight";

export default function Filters({ setShowTable }) {
  const [state, setState] = React.useState({
    left: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === "keydown" && (event.key === "Tab" || event.key === "Shift")) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const [selectedCrop, setSelectedCrop] = React.useState("");
  const [selectedYear, setSelectedYear] = React.useState("");
  const [selectedSeason, setSelectedSeason] = React.useState("");
  const [selectedCategory, setSelectedCategory] = React.useState("");
  const [selectedSubcategory, setSelectedSubcategory] = React.useState("");

  const handleChange = (event, setState) => {
    setState(event.target.value);
  };

  const subcategoryOptions = {
    Traits: ["Trait 1", "Trait 2", "Trait 3"],
    Experiments: ["Experiment 1", "Experiment 2", "Experiment 3"],
    "Crop Material Stage": ["Stage 1", "Stage 2", "Stage 3"],
    "Growth Stage": ["Growth 1", "Growth 2", "Growth 3"],
  };

  const filters = (anchor) => (
    <Box sx={{ width: anchor === "top" || anchor === "bottom" ? "auto" : 360 }} role="presentation" m={3}>
      <Box p={1.2}>
        <FormControl fullWidth mb={2}>
          <InputLabel id="crop-select-label">Crop</InputLabel>
          <Select
            labelId="crop-select-label"
            value={selectedCrop}
            label="Crop"
            onChange={(event) => handleChange(event, setSelectedCrop)}
          >
            <MenuItem value={"crop1"}>Crop 1</MenuItem>
            <MenuItem value={"crop2"}>Crop 2</MenuItem>
            <MenuItem value={"crop3"}>Crop 3</MenuItem>
          </Select>
        </FormControl>

        <Box mb={3} />
        <FormControl fullWidth mb={2}>
          <InputLabel id="year-select-label">Year</InputLabel>
          <Select
            labelId="year-select-label"
            value={selectedYear}
            label="Year"
            onChange={(event) => handleChange(event, setSelectedYear)}
          >
            <MenuItem value={"2021"}>2021</MenuItem>
            <MenuItem value={"2022"}>2022</MenuItem>
            <MenuItem value={"2023"}>2023</MenuItem>
          </Select>
        </FormControl>

        <Box mb={3} />
        <FormControl fullWidth mb={2}>
          <InputLabel id="season-select-label">Season</InputLabel>
          <Select
            labelId="season-select-label"
            value={selectedSeason}
            label="Season"
            onChange={(event) => handleChange(event, setSelectedSeason)}
          >
            <MenuItem value={"spring"}>Spring</MenuItem>
            <MenuItem value={"summer"}>Summer</MenuItem>
            <MenuItem value={"autumn"}>Autumn</MenuItem>
            <MenuItem value={"winter"}>Winter</MenuItem>
          </Select>
        </FormControl>
        <Box mb={3} />
      </Box>

      <Divider />
      <Box mb={3} />

      <Box sx={{ backgroundColor: "#f1f1f1", borderRadius: "6px" }} p={1.2}>
        <FormControl fullWidth mb={2}>
          <InputLabel id="category-select-label">Categories</InputLabel>
          <Select
            labelId="category-select-label"
            value={selectedCategory}
            label="Categories"
            onChange={(event) => handleChange(event, setSelectedCategory)}
          >
            <MenuItem value={"Traits"}>Traits</MenuItem>
            <MenuItem value={"Experiments"}>Experiments</MenuItem>
            <MenuItem value={"Crop Material Stage"}>Crop Material Stage</MenuItem>
            <MenuItem value={"Growth Stage"}>Growth Stage</MenuItem>
          </Select>
        </FormControl>

        {selectedCategory && (
          <>
            <Box mt={2.5} />
            <FormControl fullWidth mb={2}>
              <InputLabel id="subcategory-select-label">{`Select ${selectedCategory}`}</InputLabel>
              <Select
                labelId="subcategory-select-label"
                value={selectedSubcategory}
                label={`Select ${selectedCategory}`}
                onChange={(event) => handleChange(event, setSelectedSubcategory)}
              >
                {subcategoryOptions[selectedCategory].map((option) => (
                  <MenuItem key={option} value={option}>
                    {option}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </>
        )}
      </Box>

      <Box mt={4} sx={{ float: "right" }}>
        <Button variant="contained" onClick={() => setShowTable(true)}>
          Apply
        </Button>
      </Box>
    </Box>
  );

  const anchor = "left";

  return (
    <div>
      <Button onClick={toggleDrawer(anchor, !state[anchor])} variant="contained">
        <KeyboardArrowRightIcon />
      </Button>
      <Drawer anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
        {filters(anchor)}
      </Drawer>
    </div>
  );
}
