import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";

function createData(subsiteInfo, trait1, trait2, trait3, trait4, trait5) {
  return { subsiteInfo, trait1, trait2, trait3, trait4, trait5 };
}

const rows = [
  createData("Plots assigned", 450, 300, 250, 1500, 700),
  createData("Collection Growth Stage", "V6", "V5", "V9", "V9", "V9"),
  createData("Predicted Date", "6/17", "6/25", "7/1", "7/1", "7/22"),
  createData("Time Plot", "", "", "", "", ""),
];

function getRandomColor() {
  const randomNumber = Math.random();
  if (randomNumber < 0.33) {
    return "#4caf5087";
  } else if (randomNumber < 0.66) {
    return "#ffeb3b87";
  } else {
    return "#f4433687";
  }
}

export default function SubTable({ rowData }) {
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>{rowData?.subsites}</TableCell>
            <TableCell align="right">Trait 1</TableCell>
            <TableCell align="right">Trait 2</TableCell>
            <TableCell align="right">Trait 3</TableCell>
            <TableCell align="right">Trait 4</TableCell>
            <TableCell align="right">Trait 5</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.name} sx={{ "&:last-child td, &:last-child th": { border: 0 } }}>
              <TableCell component="th" scope="row">
                {row.subsiteInfo}
              </TableCell>
              <TableCell
                align="right"
                sx={{
                  backgroundColor:
                    row.subsiteInfo === "Collection Growth Stage" || row.subsiteInfo === "Predicted Date"
                      ? getRandomColor()
                      : "inherit",
                  border: "1px solid white", // Add this line
                }}
              >
                {row.trait1}
              </TableCell>
              <TableCell
                align="right"
                sx={{
                  backgroundColor:
                    row.subsiteInfo === "Collection Growth Stage" || row.subsiteInfo === "Predicted Date"
                      ? getRandomColor()
                      : "inherit",
                  border: "1px solid white", // Add this line
                }}
              >
                {row.trait2}
              </TableCell>
              <TableCell
                align="right"
                sx={{
                  backgroundColor:
                    row.subsiteInfo === "Collection Growth Stage" || row.subsiteInfo === "Predicted Date"
                      ? getRandomColor()
                      : "inherit",
                  border: "1px solid white", // Add this line
                }}
              >
                {row.trait3}
              </TableCell>
              <TableCell
                align="right"
                sx={{
                  backgroundColor:
                    row.subsiteInfo === "Collection Growth Stage" || row.subsiteInfo === "Predicted Date"
                      ? getRandomColor()
                      : "inherit",
                  border: "1px solid white", // Add this line
                }}
              >
                {row.trait4}
              </TableCell>
              <TableCell
                align="right"
                sx={{
                  backgroundColor:
                    row.subsiteInfo === "Collection Growth Stage" || row.subsiteInfo === "Predicted Date"
                      ? getRandomColor()
                      : "inherit",
                  border: "1px solid white", // Add this line
                }}
              >
                {row.trait5}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
