import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import Button from "@mui/material/Button";
import SubTable from "./SubTable";
import { Box } from "@mui/system";
import { LinearProgress, Typography } from "@mui/material";

function createData(subsites, currentGrowthStage, visualCropRating, progress1, progress2, progress3) {
  return { subsites, currentGrowthStage, visualCropRating, progress1, progress2, progress3 };
}

const rows = [
  createData("Subsite 1", "V7", 3, 90, 50, 80),
  createData("Subsite 2", "V9", 1, 20, 27, 50),
  createData("Subsite 3", "V8", 5, 50, 67, 23),
  createData("Subsite 4", "V7", 4, 90, 57, 63),
  createData("Subsite 5", "V6", 9, 80, 97, 93),
];

export default function MainTable() {
  const [open, setOpen] = React.useState(false);
  const [selectedRow, setSelectedRow] = React.useState(null); // Add this state variable to store the selected row data

  const handleClickOpen = (row) => {
    setSelectedRow(row); // Set the selected row data when opening the dialog
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const getRatingColor = (rating) => {
    if (rating <= 3) return "#4caf5087";
    else if (rating <= 5) return "#ffeb3b87";
    else return "#f4433876";
  };

  const ProgressBar = ({ value }) => (
    <Box sx={{ width: "100%", display: "flex", alignItems: "center" }}>
      <LinearProgress
        variant="determinate"
        value={value}
        color={value < 50 ? "primary" : value < 80 ? "warning" : "secondary"}
        sx={{ flexGrow: 1, marginRight: 1, height: "6px" }}
      />
      <Typography variant="body2">{value}</Typography>
    </Box>
  );

  return (
    <>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Subsites</TableCell>
              <TableCell align="right">Current Growth Stage</TableCell>
              <TableCell align="right">Visual Crop Rating</TableCell>
              <TableCell align="right">Progress 1</TableCell>
              <TableCell align="right">Progress 2</TableCell>
              <TableCell align="right">Progress 3</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <TableRow key={row.name} sx={{ "&:last-child td, &:last-child th": { border: 0 } }}>
                <TableCell component="th" scope="row">
                  <Button onClick={() => handleClickOpen(row)}>{row.subsites}</Button>
                </TableCell>
                <TableCell align="right">{row.currentGrowthStage}</TableCell>
                <TableCell align="right" width={80} sx={{ backgroundColor: getRatingColor(row.visualCropRating) }}>
                  {row.visualCropRating}
                </TableCell>
                <TableCell align="right">
                  <ProgressBar value={row.progress1} />
                </TableCell>
                <TableCell align="right">
                  <ProgressBar value={row.progress2} />
                </TableCell>
                <TableCell align="right">
                  <ProgressBar value={row.progress3} />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <Dialog open={open} onClose={handleClose} maxWidth="lg">
        <DialogTitle>{`${selectedRow?.subsites} Details`}</DialogTitle>
        <DialogContent>
          <SubTable rowData={selectedRow} /> {/* Pass the selected row data as a prop */}
        </DialogContent>
      </Dialog>
    </>
  );
}
